// ==========================================================================
// Global Imports
// ==========================================================================

@import 'https://fonts.googleapis.com/css?family=PT+Sans:400,700';

body {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100vh;
	font-family: 'PT Sans';
	font-size: 18px;
}
.tiles {
	padding: 6vh 16vw;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
	position: relative;
}
.tile {
	margin: 15px;
	cursor: pointer;
	overflow: hidden;
	width: 18vw;
	height: 18vw;
	img {
        width: 18vw;
        height: 18vw;
		width: 100%;
		transition: transform 500ms ease;
		&:hover {
			position: absolute;
			justify-content: center;
			transform: scale(1.1);
			z-index: 4;
		}
	}
}

.active {
    img {        
        width: '62vw';
        height: '62vw';
    }
	position: 'absolute';
	top: '50%';
	left: '50%';
	margin: '0';
	marginTop: '-31vw';
	marginLeft: '-31vw';
	boxShadow: '0 0 40px 5px rgba(0, 0, 0, 0.3)',;
	transform: 'none';
}

